
.Category {
  width: 15%;
  height: auto;
  float: left;
  border: 1px solid black;
}
.Category-items {
  width: 100%;
  float: left;
}
.Category-items li {
    width: 100%;
    height: 40px;
    float: left;
    list-style: none;
    line-height: 40px;
    text-align: center;
    border-bottom: 1px solid black;
}
.Category-items li:hover {
      background: black;
      color: white;
      cursor: pointer;
}



