
.Header {
  width: 100%;
  height: 100px;
  float: left;
  position: relative;
  background: blue;
}
.Header-search {
    width: 300px;
    height: 100%;
    margin: auto;
}
.Header-search input {
      width: 100%;
      height: 40px;
      background: white;
      border: none;
      outline: none;
      margin-top: 28px;
      text-indent: 5px;
}
.Header-cart {
    width: 200px;
    height: 100%;
    position: absolute;
    right: 0;
    top: 0;
}
.Header-cart i {
      height: 30px;
      margin-top: 38px;
      padding-left: 10px;
      color: white;
      cursor: pointer;
      position: relative;
}
.Header-cart i span {
        width: 15px;
        height: 15px;
        position: absolute;
        right: -5%;
        top: -18%;
        border-radius: 100%;
        line-height: 15px;
        font-size: 12px;
        font-family: arial;
        color: white;
        text-align: center;
        z-index: 20;
        background-color: red;
}
.Search-form {
  position: relative;
}
.Searched-data {
  width: 100%;
  height: auto;
  float: left;
  background: white;
}
.Searched-data-item {
    width: 100%;
    height: 40px;
    float: left;
    border-bottom: 1px solid black;
}
.Searched-data-item p {
      text-indent: 20px;
      text-transform: capitalize;
      line-height: 40px;
      font-size: 18px;
      font-family: arial;
}
.item-pic {
  width: 40px;
  height: 100%;
  float: left;
  background-size: cover;
}



