
.Product {
  width: 84.8%;
  height: auto;
  float: left;
  background: orange;
}
.Product-items {
    width: 30%;
    height: 300px;
    float: left;
}
.Product-items h3 {
      font-family: arial;
      text-indent: 5px;
}
.Product-items p {
      color: white;
      padding-left: 5px;
      margin-top: 10px;
}
.Product-items i {
      color: white;
      margin-top: 10px;
      text-indent: 5px;
      cursor: pointer;
}
.Product-pic {
    width: 100%;
    height: 60%;
    float: left;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
}



